import React from 'react';

import {
  makeStyles,
  List,
  ListItem,
  Collapse,
  ListItemText,
  ListSubheader,
  Badge,
} from '@material-ui/core';
import clsx from 'clsx';
import { useSelector } from 'react-redux';
import {
  findCategoryRouteMapString,
  findChapterNoFromKuralNo,
} from 'src/lib/api';
import { Kural, BaseType } from 'src/lib/api';
import { useBaseItem } from 'src/lib/hooks';
import { useNavigation } from 'src/lib/hooks';
import { ROUTE_CONSTANTS } from 'src/routes';
import { AppState } from 'src/state';

import { AnimatedExpandedIcon } from '../common/AnimatedExpandedIcon';
import ListView from '../common/ListView';
import KuralCard from './KuralCard';

const useStyles = makeStyles((theme) => ({
  root: {
    // marginTop: theme.spacing(2),
    // padding: theme.spacing(1),
  },
  collapsableGroup: {
    // marginBottom: theme.spacing(1),
  },
  list: {
    '& .sub-header': {
      textAlign: 'left',
      color: 'brown',
      lineHeight: '2rem',
      padding: `0px 0 0 ${theme.spacing(0.5)}px`,
    },
  },
  listItemTitle: {
    '& .badge-placeholder': {
      height: '0.5rem',
    },
    '& .toggle-icon-container': {
      marginLeft: theme.spacing(1.5),
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
  },
  listTitleBadge: {
    opacity: 0.6,
    fontSize: '0.6rem',
    height: '130%',
    width: '120%',
  },
  kural: {
    padding: `0 0 0 ${theme.spacing(1)}px`,
    '&:not(:last-child)': {
      marginBottom: theme.spacing(1),
    },
  },
  chapFavorites: {
    padding: 0,
    '& li': {
      paddingLeft: theme.spacing(0.5),
      paddingRight: theme.spacing(0.5),
    },
  },
}));

type FavoritesViewerProps = {
  className?: string;
  expand?: boolean;
};

const FavoritesViewer: React.FC<FavoritesViewerProps> = (
  props: FavoritesViewerProps
) => {
  const [getChapter] = useBaseItem('chapter');
  const { kural: KURAL_IDS, chapter: CHAPTER_IDS } = useSelector(
    (st: AppState) => st.user.favorites
  );
  const INDEX_MAP = useSelector((st: AppState) => st.kuralIndexMap.index);
  const KURALS = useSelector((st: AppState) => st.kuralIndexMap.kurals);
  const [chapterKuralMap, setchapterKuralMap] = React.useState<
    Record<number, BaseType[]>
  >({});
  const [isKuralVisible, setKuralVisible] = React.useState<boolean>(
    props.expand ?? true
  );
  const [isChapterVisible, setChapterVisible] = React.useState<boolean>(
    props.expand ?? false
  );
  const [navigateTo] = useNavigation();
  const classes = useStyles();
  const onChapterSelected = (item: BaseType): void => {
    if (INDEX_MAP) {
      navigateTo(
        `${ROUTE_CONSTANTS.LIST}/${findCategoryRouteMapString(INDEX_MAP, {
          chapter: item.id,
        })}`
      )();
    }
  };
  const onKuralSelected = (item: BaseType): void => {
    if (INDEX_MAP) {
      navigateTo(
        `${ROUTE_CONSTANTS.LIST}/${findCategoryRouteMapString(INDEX_MAP, {
          kural: item.id,
        })}`
      )();
    }
  };
  React.useEffect(() => {
    const chapMap = KURAL_IDS.reduce((chapMap, kId) => {
      const chap = findChapterNoFromKuralNo(kId);
      return {
        ...chapMap,
        [chap]: [...(chapMap[chap] || []), KURALS[kId]],
      };
    }, {} as Record<number, BaseType[]>);
    setchapterKuralMap(chapMap);
  }, [KURAL_IDS, KURALS]);
  const noRecordsFound = <h1>None Found</h1>;

  const GroupTitleMaker = (
    isVisible: boolean,
    onClick: () => void,
    title: React.ReactNode,
    badgeContent: React.ReactNode
  ) => (
    <ListItem
      selected={isVisible}
      className={classes.listItemTitle}
      button
      onClick={onClick}>
      <ListItemText inset={true} primary={title} />
      <Badge
        badgeContent={badgeContent}
        classes={{ badge: classes.listTitleBadge }}
        color='secondary'
        showZero={false}
        max={10}
        invisible={isVisible}>
        <div className='badge-placeholder'></div>
      </Badge>
      <div className={`toggle-icon-container`}>
        <AnimatedExpandedIcon isExpanded={isVisible} />
      </div>
    </ListItem>
  );
  return (
    <div className={clsx(props.className, classes.root)}>
      <List className={classes.list} disablePadding>
        {GroupTitleMaker(
          isKuralVisible,
          () => {
            setKuralVisible(!isKuralVisible);
          },
          'Kurals',
          KURAL_IDS.length
        )}
        <Collapse
          in={isKuralVisible}
          style={{ paddingBottom: isKuralVisible ? 8 : 0 }}
          className={classes.collapsableGroup}
          timeout='auto'>
          {Object.keys(chapterKuralMap).length
            ? Object.keys(chapterKuralMap).map((chapId) => {
                const chapterInfo = getChapter(parseInt(chapId));
                return (
                  <List
                    component='div'
                    className='sub-list'
                    key={chapId}
                    disablePadding>
                    {chapterInfo && (
                      <ListSubheader
                        className='sub-header'
                        disableSticky
                        onClick={(): void => onChapterSelected(chapterInfo)}>
                        {chapterInfo.value}
                      </ListSubheader>
                    )}

                    {chapterKuralMap[parseInt(chapId)].map((item: BaseType) => (
                      <KuralCard
                        className={classes.kural}
                        key={item.id}
                        kural={item}
                        onClick={onKuralSelected}
                      />
                    ))}
                  </List>
                );
              })
            : noRecordsFound}
        </Collapse>
        {GroupTitleMaker(
          isChapterVisible,
          () => {
            setChapterVisible(!isChapterVisible);
          },
          'Chapters',
          CHAPTER_IDS.length
        )}
        <Collapse
          in={isChapterVisible}
          style={{ paddingBottom: isChapterVisible ? 8 : 0 }}
          className={classes.collapsableGroup}
          timeout='auto'>
          {CHAPTER_IDS.length > 0 && (
            <ListView
              className={classes.chapFavorites}
              items={
                CHAPTER_IDS.sort()
                  .map((ch) => getChapter(ch))
                  .filter((ch) => ch !== null) as BaseType[]
              }
              onItemSelected={onChapterSelected}
            />
          )}
          {CHAPTER_IDS.length === 0 && noRecordsFound}
        </Collapse>
      </List>
    </div>
  );
};

export default FavoritesViewer;
